* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  text-align: center;
  font-family: 'Josefin Sans', 'Balsamiq Sans', sans-serif;
}

header {
  font-weight: 700;
  font-size: 30px;
  padding: 20px;
  margin-bottom: 15px;
  background-color: #7BA7AB;
  color: #fff1e6
}

.Search {
position: relative;
width: 320px;
margin: auto;
padding: 15px;
}

input {
width: 100%;
height: 42px;
padding-left: 10px;
border: 1px solid #7BA7AB;
border-radius: 5px;
outline: none;
background: #F9F0DA;
color: #9E9C9C;
}

button {
position: absolute; 
top: 15px;
right: 15px;
width: 42px;
height: 42px;
border: none;
background: #7BA7AB;
border-radius: 0 5px 5px 0;
cursor: pointer;
}

button:before {
content: "\f002";
font-family: FontAwesome;
font-size: 16px;
color: #F9F0DA;
}

.Person {
  display: flex;
  align-items: center;
  padding: 15px;
  margin: 10px;
  background-color: #c5dedd;
  border-radius: 10px;
}

.female::before {
  content: "\f182";
  font-family: FontAwesome;
  font-size: 40px;
  color: #ff006e;
  margin-right: 17px;
}

.male::before {
  content: "\f183";
  font-family: FontAwesome;
  font-size: 40px;
  color: #3a86ff;
  margin-right: 20px;
  margin-left: 3px;
}

.anon::before {
  content: "\f128";
  font-family: FontAwesome;
  font-size: 40px;
  color: #00afb9;
  margin-right: 13px;
}

.phone {
  display: block;
  padding-left: 10px;
}

.Contacts {
  margin: auto;
  padding: 10px;
  width: 400px;
}

.aligned {
  display: flex;
  align-items: center;
}
 
span {
  padding: 10px;
}